.base {
  background: #cacaca;
  width: 20vmin;
  border-radius: 27vmin;
  box-shadow: 0 6vmin 0.15vmin 0vmin rgb(150, 149, 149),
    0 4vmin 0.15vmin 0vmin #777, 0 2vmin 0.15vmin 0vmin #777;
  padding: 0vmin 2vmin 2vmin 2vmin;
  z-index: 1;
  -webkit-transform: rotateX(65deg) rotateY(5deg) rotateZ(1deg);
          transform: rotateX(65deg) rotateY(5deg) rotateZ(1deg);
  margin-top: -4.5vmin;
  margin-right: -2.5vmin;
  height: 22vmin;
}

button#activate {
  background: #3c6acf;
  border: 0;
  width: 20vmin;
  height: 19vmin;
  border-radius: 100%;
  position: relative;
  cursor: pointer;
  outline: none;
  z-index: 2;
  box-shadow: 0 4vmin 0.15vmin 0vmin #0243a5, 0 2vmin 0.15vmin 0vmin #0003af;
  top: -2.5vmin;
  border: 0.5vmin solid #012892a1;
  transition: all 0.25s ease 0s;
}

button#activate:hover {
  box-shadow: 0 3vmin 0.15vmin 0vmin #180197, 0 1vmin 0.15vmin 0vmin #180197;
  top: -1.5vmin;
  transition: all 0.5s ease 0s;
}
.modal {
  color: white;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 102;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal[datatype="1"] {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 102;
  width: 100vw;
  height: 100vh;
  -webkit-animation-name: wobble2;
          animation-name: wobble2;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes wobble2 {
  30% {
    visibility: hidden;
    background: #1900ff;
  }
  50% {
    visibility: visible;
    background: #000000;
  }
  75% {
    visibility: hidden;
    background: #ffffff;
  }
  100% {
    visibility: visible;
    background: #000000;
  }
}
@keyframes wobble2 {
  30% {
    visibility: hidden;
    background: #1900ff;
  }
  50% {
    visibility: visible;
    background: #000000;
  }
  75% {
    visibility: hidden;
    background: #ffffff;
  }
  100% {
    visibility: visible;
    background: #000000;
  }
}

@-webkit-keyframes wobble {
  25% {
    visibility: visible;
    background: #ffffff;
  }
  50% {
    background: #ff0000;
    -webkit-transform: scale(1000);
            transform: scale(1000);
  }
  75% {
    background: #ffffff;
    -webkit-transform: scale(99999);
            transform: scale(99999);
  }
  100% {
    background: #000000;
    -webkit-transform: scale(10);
            transform: scale(10);
    visibility: visible;
  }
}

@keyframes wobble {
  25% {
    visibility: visible;
    background: #ffffff;
  }
  50% {
    background: #ff0000;
    -webkit-transform: scale(1000);
            transform: scale(1000);
  }
  75% {
    background: #ffffff;
    -webkit-transform: scale(99999);
            transform: scale(99999);
  }
  100% {
    background: #000000;
    -webkit-transform: scale(10);
            transform: scale(10);
    visibility: visible;
  }
}
.base::after {
  content: "";
  position: fixed;
  left: 40%;
  top: 40%;
  z-index: 99;
  visibility: hidden;
}
.base[datatype="1"]::after {
  background: #000000;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  -webkit-animation-name: wobble;
          animation-name: wobble;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}
