.warning {
  position: absolute;
  z-index: 0;
  width: 45vmin;
  height: 45vmin;
  background: repeating-linear-gradient(
    -45deg,
    rgb(0, 21, 66),
    black 3vmin,
    rgb(0, 153, 255) 3vmin,
    rgb(96, 106, 122) 6vmin
  );
  -webkit-transform: rotateX(-28deg) rotateY(27deg) rotateZ(5deg)
    rotate3d(1, 0, 0, 90deg);
          transform: rotateX(-28deg) rotateY(27deg) rotateZ(5deg)
    rotate3d(1, 0, 0, 90deg);
  /* transform: rotateX(-35deg) rotateY(45deg) rotateZ(0deg)
    rotate3d(1, 0, 0, 90deg); */
  border-radius: 3px;
  box-shadow: 0 0 0 7vmin #000000;
}

.warning:before {
  content: "";
  width: 80%;
  height: 80%;
  background: linear-gradient(45deg, #000000 0%, #414141 74%);
  float: left;
  margin-top: 10%;
  margin-left: 10%;
  border: 1vmin solid rgb(255, 255, 255);
  border-radius: 1vmin;
  box-sizing: border-box;
}

