  54% {
    bottom: 150px;
    opacity: 0;
  }
}

.typewriter h1 {
  color: rgb(0, 0, 0);
  overflow: hidden;
  border-right: 0.15em solid rgb(0, 0, 0);
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.15em;
  -webkit-animation: typing 3.5s steps(50, end) infinite,
    blink-caret 0.5s step-end infinite;
          animation: typing 3.5s steps(50, end) infinite,
    blink-caret 0.5s step-end infinite;
}

/* The typing effect */
@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: rgb(0, 0, 0);
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: rgb(0, 0, 0);
  }
}
.sky h1 {
  margin-top: 5vh;
  text-align: center;
